@import "https://fonts.googleapis.com/css?family=Bubbler+One|Dawning+of+a+New+Day|Text+Me+One&display=swap";
@import "https://fonts.googleapis.com/css?family=Cherry+Swash:700&display=swap";
:root {
  --overlay: #ffffff80;
  --text-color: #2c3e50;
  --color-1: #aba104;
  --color_2: #153607;
  --color-3: #6d6e71;
  --menu-h-1: 100px;
  --menu-h-2: 70px;
}

html, body {
  box-sizing: border-box;
  text-align: center;
  scroll-behavior: smooth;
  color: var(--text-color, #333);
  background: #f0ffd7;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Bubbler One, sans-serif;
  font-size: 1.2rem;
  line-height: 150%;
}

body {
  margin: var(--menu-h-1) auto 0;
}

* {
  box-sizing: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
}

ul {
  list-style: none;
}

.anchor {
  height: var(--menu-h-1);
  visibility: hidden;
  margin-top: -100px;
  display: block;
}

.container {
  position: relative;
}

#header-bar {
  height: var(--menu-h-1);
  z-index: 500;
  background: #fff;
  align-items: center;
  width: 100%;
  padding: 0 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#header-bar img {
  height: var(--menu-h-1);
  width: var(--menu-h-1);
}

.menu-bar {
  flex: 1;
  justify-content: space-around;
  margin-left: .5rem;
  position: relative;
}

.menu-items {
  max-height: auto;
  width: 100%;
  display: flex;
  overflow: hidden;
}

.menu-items li {
  margin: 0 .1rem;
}

.menu-items li:not(:last-child) {
  flex: none;
}

.menu-items li:last-child {
  text-align: right;
  flex: 1 0 auto;
  justify-content: right;
}

.menu-items a {
  background: #e0eb4e;
  border-radius: 5px;
  margin: 0 .5rem;
  padding: .4rem;
}

.menu-items a:hover {
  background: #bbb;
}

.menu-btn {
  display: none;
}

.content, .text {
  letter-spacing: .1rem;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

article.text {
  color: #000;
  width: 50%;
  font-family: Cherry Swash, cursive;
  font-weight: 900;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

article.text h2 {
  margin-bottom: 1rem;
}

.overlay {
  background: var(--overlay);
  height: 100%;
}

section {
  height: calc(100vh - var(--menu-h-1));
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  position: relative;
}

#section-1 {
  background-image: url("danny-wage-Q4vWchoavUs-unsplash.4a4cf9b6.jpg");
}

#section-2 {
  background-image: url("four-green-yarns-on-chopping-board-2062061.acb8bf5e.jpg");
  height: 60vh;
}

#section-3 {
  height: 100%;
}

#section-4 {
  background-image: url("janko-ferlic-specialdaddy-jjDqU0P0-SM-unsplash.c668560f.jpg");
}

#section-4 article {
  text-shadow: -1px -1px 1px #fffc, 1px 1px 1px #fffc;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.text {
  flex-direction: column;
  place-content: center space-evenly;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.text h3 {
  margin: .5rem 0;
}

#footer-bar {
  background: var(--text-color);
  color: #f0f8ff;
  justify-content: space-around;
  width: 100%;
  margin-top: .5rem;
  padding: .3rem;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

#footer-bar img {
  width: 30px;
  margin-right: .5rem;
}

#footer-bar a {
  align-items: center;
  display: flex;
}

.photo-gallery {
  grid-row-gap: .3rem;
  grid-template-rows: repeat(auto-fit, minmax(200px, 1fr));
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  column-gap: .3rem;
  width: 100%;
  height: 100%;
  padding: .5rem;
  display: grid;
}

.photo {
  opacity: .9;
  height: 200px;
}

.photo:hover {
  opacity: 1;
  border: none;
  transform: scale(1.025);
}

.photo img {
  object-fit: cover;
  object-position: center;
  height: 100%;
}

.photo-gallery div:nth-child(2), .photo-gallery div:nth-child(10) {
  grid-column: auto / span 2;
}

#modal {
  z-index: 100;
  background: #000c;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 2rem;
}

#modal.show {
  justify-content: center;
  align-items: center;
  display: flex;
}

#modal img {
  object-fit: cover;
  object-position: center;
  background: #000c;
  border: 2px solid #fff;
  max-width: 100%;
  max-height: 100%;
  padding: 5px;
}

#modal-content {
  width: 70%;
  height: 70%;
  position: relative;
}

#close {
  cursor: pointer;
  text-align: center;
  background: #eee;
  border-radius: 2px;
  width: 24px;
  height: 24px;
  font-family: arial, sans-serif;
  font-weight: 900;
  position: absolute;
  top: 0;
  right: 0;
}

@media (width >= 1190px) {
  .photo-gallery {
    width: 80%;
    margin: auto;
  }
}

@media (width <= 760px) {
  html, body {
    box-sizing: border-box;
    width: 100%;
    min-height: 100vh;
    font-size: 1rem;
    line-height: 120%;
  }

  body {
    margin: var(--menu-h-2) auto 0;
  }

  #header-bar {
    height: var(--menu-h-2);
  }

  #header-bar img {
    height: var(--menu-h-2);
    width: var(--menu-h-2);
  }

  .menu-items {
    max-height: 0;
  }

  .menu-icon {
    float: right;
    cursor: pointer;
    z-index: 5;
    padding: 1.5rem;
    position: relative;
  }

  .menu-icon .nav-icon {
    background: #5f9ea0;
    width: 18px;
    height: 2px;
    transition: background .2s ease-out;
    display: block;
    position: relative;
  }

  .menu-icon .nav-icon:before, .menu-icon .nav-icon:after {
    content: "";
    background: #5f9ea0;
    width: 100%;
    height: 100%;
    transition: all .2s ease-out;
    display: block;
    position: absolute;
  }

  .menu-icon .nav-icon:before {
    top: 5px;
  }

  .menu-icon .nav-icon:after {
    top: -5px;
  }

  .menu-btn:checked ~ .menu-items {
    background: #e0eb4e33;
    flex-direction: column;
    align-items: flex-end;
    height: 15rem;
    max-height: 25rem;
    position: fixed;
    top: 5.2rem;
    right: 2rem;
  }

  .menu-btn:checked ~ .menu-icon .nav-icon {
    background: none;
  }

  .menu-btn:checked ~ .menu-icon .nav-icon:after {
    top: 0;
    transform: rotate(45deg);
  }

  .menu-btn:checked ~ .menu-icon .nav-icon:before {
    top: 0;
    transform: rotate(-45deg);
  }

  .menu-items li {
    text-align: left;
  }

  .menu-items li:not(:last-child) {
    flex: 1 0 auto;
  }

  .menu-items li:last-child {
    text-align: left;
    flex: 1 0 auto;
  }

  .menu-items a {
    margin: .5rem;
    padding: .5rem;
  }

  .anchor {
    height: var(--menu-h-2);
    margin-top: -70px;
  }

  .content, .text {
    width: 95%;
    padding: 0 1rem;
  }

  section {
    height: calc(100vh - var(--menu-h-2));
  }

  article.text {
    width: 90%;
  }

  .photo-gallery div:nth-child(2), .photo-gallery div:nth-child(10) {
    grid-column: auto / span 1;
  }
}

@media (width <= 760px) and (orientation: portrait) {
  .menu-bar a {
    margin: 0 .1rem;
    padding: .2rem;
  }

  #modal img {
    max-width: 100%;
    max-height: 100%;
    padding: 2px;
  }
}
